.graphContainer {
  position: fixed;
  z-index: -1;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: white;
}

.shipNameContainer {
  position: fixed;
  top: 6%;
  left: 50%;
  width: 5%;
  display: flex;
  flex-direction: column;
}


.controlButton {
  background-color: white;
  border-radius: 8px;
  color: black;
  border: 2px solid #008CBA;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  opacity: 0.8;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2), 0 1px 3px 0 rgba(0,0,0,0.19);
}

.controlButton:active {
  box-shadow: 0 2px rgba(0,0,0,0.2);
  transform: translateY(2px);
}

.analysisButtons {
  position: fixed;
  bottom: 10px;
  width: 30%;
  left: 50%;
  margin-left: -15%;
  text-align: center;
}

.bottomContainer {
  position: fixed;
  bottom: 10px;
  width: 50%;
  left: 50%;
  margin-left: -25%;
  text-align: center;
}

.sidePanel {
  margin-top: 10%;
  width: 5%;
  position: fixed;
  z-index: 1;
  top: 20px;
  right: 10px;
  /* padding: 8px 0; */
}

.filterText {
  color: darkgreen;
  font-size: small;
}

.citationText {
  color: darkred;
  font-size: smaller;
}

.closePanelButton {
  position: absolute;
  background-color: green;
  color: white;
  text-align: center;
  text-decoration: none;
  border-radius: 10%;
  z-index: 300;
  top: 0px;
  right: 0px;
  padding: 2px 5px;
}

.closeOverlayButton {
  position: absolute;
  background-color: white;
  color: black;
  text-align: center;
  text-decoration: none;
  border-radius: 50%;
  z-index: 300;
  top: -10px;
  right: -10px;
  padding: 2px 5px;
}

.modal {
  position: absolute;
  z-index: 201;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  background-color: white;
}

.modalOverlay {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.8);
  opacity: 0;
  transition: opacity 200ms ease-in-out;
}

.modalOverlayAfterOpen{
  opacity: 1;
}

.modalOverlayBeforeClose{
  opacity: 0;
}

@media(hover: hover) and (pointer: fine){
  .controlButton:hover {
    background-color: #008CBA;
    opacity: 1.0;
    color: white;
  }
  .closeOverlayButton:hover {
    padding: 2px 5px;
    background-color: red;
    color: white;
  }
  .closePanelButton:hover {
    padding: 2px 5px;
    background-color: red;
    color: white;
  }
}
